body{
    direction: ltr;
    font-family: "Lato";
    &.over{
        overflow: hidden;
    }
}
//loader
.loader-box{
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    right: 0;
    background-color:$color1;
    z-index: 99999;
    .loader{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 200px;
        height: 200px;
        .face{
            position: absolute;
            border: 2px solid #747474;
            
        }
        .face1{
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: #090909;
            box-shadow: 0 0 10px rgba(0, 0, 0,1);    
            border-radius: 50%; 
            border-left: 2px solid $color2;
            border-top: 2px solid $color2;
            .circle{
                transform: rotate(-45deg)
            }
            animation: loader 3s linear infinite;
        }
        .face2{
            top: 30px;
            left: 30px;
            right: 30px;
            bottom: 30px;
            background-color: #090909;
            box-shadow: 0 0 10px rgba(0, 0, 0,1);    
            border-radius: 50%; 
            border-right: 2px solid $color2;
            border-bottom: 2px solid $color2;
            .circle{
                transform: rotate(-45deg)
            }   
            animation: loader 3s linear reverse infinite;
        }
        .circle{
            position: absolute;
            top: calc(50% - 1px);
            left: 50%;
            width: 50%;
            height: 2px;
            transform-origin: left;
            &::before{
                content: '';
                position: absolute;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background-color: $color2;
                top: -4px;
                right: -6px;
                box-shadow: 0 0 20px #ff0,
                            0 0 40px #ff0,
                            0 0 60px #ff0,
                            0 0 80px #ff0,
                            0 0 100px #ff0,
                            0 0 0 5px rgba(#fff,.1);
            }
        }
    }
}
//animation
@keyframes loader{
    0%{
        transform: rotate(0deg)
    }
    100%{
        transform: rotate(360deg)
    }
}
@keyframes heart{
    to
    {
        box-shadow: 0 0 0 8px rgba($color2, 0);
    }
}
@keyframes navsm1{
    0%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(10px);

    }
   100%{
    transform: translateY(10px)  rotateZ(45deg);
   
   } 
}
@keyframes navsmun{
    0%{
        transform: translateY(10px)  rotateZ(45deg);
    }
    50%{
        transform: translateY(10px)  rotateZ(0);

    }

   100%{
    transform: translateY(0)  rotateZ(0);
   
   } 
}
@keyframes navsmun2{
    0%{
        transform: translateY(-10px)  rotateZ(-45deg);
    }
    50%{
        transform: translateY(-10px)  rotateZ(0);

    }

   100%{
    transform: translateY(0)  rotateZ(0);
   
   } 
}

@keyframes navsm2{
    0%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(-10px);

    }
   100%{
    transform: translateY(-10px)  rotateZ(-45deg);
   
   } 
    
}

//nav 
.overlay{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    right: -100%;
    background-color: rgba(68, 62, 62, 0.851);
    z-index: 888;
    transition: 1s;
}
.over-active{
    right: 0;

}
.side-nav{
    position: fixed;
    width: 270px;
    height: 100%;
    left: -100%;
    z-index: 999;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 0 10px;
    transition: .7s;

    &.nav-active{
        left: 0;
    }
    .mai-nav{
        width: 100%;
        padding: 10px 20px;

        .list-nav{
            display: flex;
            flex-direction: column;
            margin: 0;
            padding: 0;
            list-style: none;
            li{
                text-transform: uppercase;
                padding: 10px 0 ;
                &:not(:last-child){
                    border-bottom: 1px solid rgb(222, 222, 222);
                }
                a{
                    text-decoration: none;
                    font-size: 16px;
                    font-family: "LatoSemibold";
                    color: #989898;
                    transition: .5s;
                    &:hover{
                        color:$color1;
                    }

                }
            }
        }
    }

    .bottom-nav{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .lang{
            display: flex;
            margin-bottom: 10px;
            a{
                position: relative;
                margin-right: 10px;
            }

        }
        .email,
        .phone{
            font-size: 14px;
            color: rgb(152, 152, 152);
            transition: .5s;
            &:hover{
                color: rgb(14, 14, 14);
            }
            display: block;
            span{
                
                font-family: "Lato";
            }
            margin-right: 17px;
            text-decoration: none;
            padding-bottom: 10px;
            padding-top: 10px;
            i{
                color: rgb(110, 110, 110);
                margin-right: 12px;                   
            }
        }

    }
}
.sm-search{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left:-100%;
    background-color: rgba(68, 62, 62, 0.1);
    z-index: 9999;
    transition: 1.3s;
    display: flex;
    justify-content: center;
    align-items: center;
    &.search-active{
        left:0;

    }
    .search-torger{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 30px;
        position: absolute;
        right: 130px;
        top: 20px;
        z-index: 999;

        span{
            display: block;
            width: 100%;
            background-color: #fff;
            border-radius: 2px;
            display: block;
            width: 100%;
            height: 4px;
            position: absolute;
            transition: 1.1s;
            &:nth-child(1){
                top: 0;
                animation: navsm1 1s ease-in-out forwards ;

            }
            &:nth-child(2){
                top: 10px;
                opacity: 0;

            }
            &:nth-child(3){
                top: 20px;
                animation: navsm2 1s ease-in-out forwards ;

            }
        }
    }
    .search-box{
        .form{
            width: max-content;
            position: relative;
            .right,
            .left{
                content: '';
                top: 0;
                position: absolute;
                width: 30%;
                height: 100%;
                background-color: rgb(242, 242, 242);
                z-index: -1;
                transition: .6s ease-out;
            }
            .left{
                left: 0;
                border-top-left-radius: 100px;
                border-bottom-left-radius: 100px;

            }
            .right{
                right: 0;
                border-top-right-radius: 100px;
                border-bottom-right-radius: 100px;
            }
            input{
                background-color: rgb(242, 242, 242);
                width: 560px;
                @media (max-width:480px) {
                    width: 300px;
                }

                height: 50px;
                border-radius: 100px;
                border: none;
                outline: none;
                padding: 0 33px;
                transition: .5s ;
                &::placeholder{
                    font-size: 14px;
                    font-family: "Lato";
                    color: rgb(152, 152, 152);
                }
                &:focus{
                    &::placeholder{
                        color: transparent;
                    }
                }
            }
            input:focus ~ .left{
                left: -25%;
                opacity: 0;
            }   
            input:focus ~ .right{
                right: -25%;
                opacity: 0;
            }   
            button{
                background-color: transparent;
                border: none;
                outline: none;
                font-size: 16px;
                color: rgb(14, 14, 14);
                position: absolute;
                top: 50%;
                right: 17px;
                transform: translateY(-50%);
                height: 100%;
            }
        }
    }

}
