        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 0 70px;
        .owl-prev,
        .owl-next{
            background-color: rgba(68, 62, 62, 0.851) ;
            border-radius: 50%;
            width: 50px;
            height: 50px;
            box-shadow: 0 0 0 0 rgba($color2, 1);
            transition: .5s;
            @media (max-width:991px) {
                display: none;
            }
            i{
                color: #fff;
                font-size: 15px;
                line-height: 1;
            }
            &:hover{
                animation: heart .8s  cubic-bezier(.66, 0, 0, 1) infinite;
                background-color: $color2;
            }
        }
        
    }
}
.owl-carousel .owl-nav button.owl-next, .owl-carousel .owl-nav button.owl-prev, .owl-carousel button.owl-dot{
    outline: none;
    background-color: rgba(68, 62, 62, 0.851) ;
}
/*about*/
