    position: relative;
    z-index: 0;
    overflow: hidden;
    .pattern{
        position: absolute;
        left: -30%;    
        top: -85%;
        z-index: -1;
        @media (max-width:991px) {
            display: none;
         }
 
         @media (min-width:1920px) {
            left: -22%;
        }
 
          img{
            width: 80%;
            @media (min-width:1920px) {
                width: 100%;
            }
        }
    }
    .content{
        display: flex;
        padding: 20px 0 50px;
        z-index: 6;
        @media (max-width:991px) {
            text-align: center;
            justify-content: center;
            align-items: center;  
        }
        .img-box-sm{
            display: none;
            overflow: hidden;
            @media (max-width:991px) {
                display: block;
            }
            margin-top: -40px;
            margin-bottom: 15px;
            z-index: -1;
            img{
                transform: translateX(-36px);
                @media (max-width:480px) {
                    transform: translateX(-20px);
                }
    
            }
        }
        .cap{
