        justify-content: center;
        text-align: center;
    }
    .pattern{
        position: absolute;
        right: 0;
        top: -347px;
        z-index: -1;
        @media (max-width:991px) {
            right: 70%;

        }
    
    }
    .content{
        display: flex;
        align-items: center;
        @media (max-width:991px) {
            justify-content: center;
            flex-direction: column;
        }
    
        .icon{
            margin-right: 30px;
            @media (max-width:991px) {
                margin-right: 0;
                margin-bottom: 10px;

            }
        
    
            i{
                color: rgb(255, 255, 255);
                font-size: 60px;                  
            }
            
        }   
        .cap{
            @media (max-width:991px) {
                margin-bottom: 30px;
            }

            h2{
                font-size: 24px;
                color: rgb(255, 255, 255);
                text-transform: uppercase;
                font-family: "LatoHeavy";
                margin: 0;
                span{
                    display: block;
                    font-family: "LatoLight";
                    font-size: 22px;
                }                  
            }
        }     
    }
    .subscribe-form{
        display: flex;
        align-items: center;
        justify-content: center;
        .form{
            width: max-content;
            position: relative;
            input{
                background-color: rgb(255, 255, 255);
                width: 555px;
                height: 59px;
                @media (max-width:1024px) {
                    width: 455px;
                }
                @media (max-width:425px) {
                    width: 300px;
                }
        

                border-radius: 100px;
                border: none;
                padding: 0 40px;
                outline: none;
                transition: .5s;
                &::placeholder{
                    font-size: 18px;
                    font-family: "LatoLight";
                    color: rgb(144, 143, 143);
                }
                &:focus{
                    &::placeholder{
                        color: transparent;
                    }
                }
            }
            button{                  
                background-color:transparent;
                width: 150px;
                height: 45px;
                border: none;
                outline: none;
                position: absolute;
                top: 50%;
                right: 8px;
                transform: translateY(-50%);
                font-size: 14px;
                color: transparent;
                text-transform: uppercase;
                font-family: "LatoHeavy";
                border-radius: 100px;
                transition: .3s;
                @media (max-width:991px) {
                    margin-top: 10px;
                    transform: translateY(0);
                    width: 455px;
                    position: static;

                }
                @media (max-width:425px) {
                    width: 300px;
                }

                &::after,
                &::before{
                    content: attr(data-text);
                    position: absolute;
                    top: -1px;
                    width: 100%;
                    height: 100%;
                    backface-visibility: hidden;
                    transition: .5s;    
                    background-color: rgb(212, 175, 46);
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 14px;
                    color: rgb(255, 255, 255);
                    text-transform: uppercase;
                    font-family: "LatoHeavy";
                    border-radius: 100px;
    
                }
                &::before{
                    clip-path: inset(0 0 0 50%);
                    left: -.3px;
    
                }
                &::after{
                    clip-path: inset(0 50% 0 0);
                    right: -.3px;
    
                }
    
                &:hover{
                    background-color: $color1;
                    color: #fff;
                    @media (max-width:991px) {
                        background-color: #fff;
                        color: $color1;
                    }
                    &::before{
                        left: 21%;
                        opacity: 0;
                    }
                    &::after{
                        right:21%;
                        opacity: 0;
                    }
                }
            }
            .right,
            .left{
                top: 0;
                position: absolute;
                width: 50%;
                height: 100%;
                @media (max-width:991px) {
                    height: 50%;
                    width: 10%;

                }
                @media (max-width:425px) {
                    height: 50%;
                    width: 60%;

                }

                background-color: rgb(255, 255, 255);
                z-index: -1;
                transition: .5s ease-out;
            }
            .left{
                left: 0;
                border-top-left-radius: 100px;
                border-bottom-left-radius: 100px;
                @media (max-width:991px) {
                    left: 20%;
                }

            }
            .right{
                right: 0;
                @media (max-width:991px) {
                    right: 20%;
                }

                border-top-right-radius: 100px;
                border-bottom-right-radius: 100px;
            }
            input:focus ~ .left{
                left: -15%;
                @media (max-width:991px) {
                    left: 10%;
                }
                @media (max-width:425px) {
                    left: 0%;

                }

                opacity: 0;
            }   
            input:focus ~ .right{
                right: -15%;
                @media (max-width:991px) {
                    right: 10%;
                }
                @media (max-width:425px) {
                    right: 0%;

                }
                opacity: 0;

            }   
        }
    }
}
